import React, { Component } from 'react';
import axios from 'axios';
import { reactLocalStorage } from 'reactjs-localstorage';
import { withRouter } from 'react-router-dom';
import { Layout, Menu, Drawer, Button, Input } from 'antd';
import {
   StarFilled,
   SketchCircleFilled,
   VideoCameraFilled,
   GiftFilled,
   CrownFilled,
   HomeOutlined,
   LoginOutlined,
   FormOutlined,
   UserOutlined,
   BarsOutlined,
   EyeInvisibleFilled,
   RocketFilled,
   RedditCircleFilled,
   DingdingOutlined,
   ThunderboltFilled,
   GitlabFilled,
   RobotFilled,
   HourglassFilled,
   ReadFilled,
   FireFilled,
   MenuUnfoldOutlined,
} from '@ant-design/icons';
import MyInfo from '../MyInfo/MyInfo';
import './MenuBar.css';
import SFCINEMA from '../../SFCINEMA.png';

const { Header } = Layout;
const { SubMenu } = Menu;

const marvelSeriesList = [
   '',
   '',
   '',
   '',
   ' ',
   '',
   '',
   '  ',
   '',
   '',
];

const zombieAndMonsterSeriesList = [
   ' ',
   '',
   '28 ',
   ' ',
   '',
];

const alienAndSpaceSeriesList = [
   '',
   '  ',
   '',
   '',
   '',
   ' ',
   ' ',
];

const actionAndAdventureSeriesList = [
   '',
   ' ',
   '   ',
   '',
   '',
   ' ',
];

const geners = [
   [' ', <RocketFilled />],
   ['', <RedditCircleFilled />],
   [' ', <DingdingOutlined />],
   ['', <ThunderboltFilled />],
   ['', <GitlabFilled />],
   ['   AI', <RobotFilled />],
   [' ', <HourglassFilled />],
   ['', <ReadFilled />],
   ['', <EyeInvisibleFilled />],
   ['', <FireFilled />],
];

class MenuBar extends Component {
   constructor(props) {
      super(props);
      this.state = {
         visible: false,
         profile: {},
         selectKey: '',
      };
   }

   handleLogoutChange = () => {
      reactLocalStorage.remove('SFCinemaUserToken');
      location.reload(true);
   };

   NavigateToGenres = (path, key, genre) => {
      this.props.history.push(`${path}?key=${key}&genre=${genre}`);
   };

   NavigateToHighlyRataedAndReleaseOrder = (
      path,
      key,
      count,
      under,
      moreThen,
   ) => {
      this.props.history.push(
         `${path}?key=${key}&count=${count}&under=${under}&moreThen=${moreThen}`,
      );
   };

   NavigateToSeries = (path, key, seriesName) => {
      this.props.history.push(`${path}?key=${key}&seriesName=${seriesName}`);
   };
   NavigateToOperatorAndMasterpiece = (path, key, count) => {
      this.props.history.push(`${path}?key=${key}&count=${count}`);
   };

   showDrawer = () => {
      const accessToken = reactLocalStorage.get('SFCinemaUserToken');

      if (this.props.isLogin) {
         if (accessToken) {
            axios
               .get('http://54.180.32.31:5000/user/profile', {
                  headers: {
                     Authorization: 'Bearer ' + accessToken,
                  },
               })
               .then(({ data }) => {
                  this.setState({
                     profile: data,
                  });
               });
         }
      }

      this.setState({
         visible: true,
      });
   };

   onClose = () => {
      this.setState({
         visible: false,
      });
   };

   render() {
      const { handleLoginChange, isLogin } = this.props;

      let currentpath = window.location.pathname;
      let currentParams = window.location.search;
      currentpath =
         currentpath === '/series' || currentpath === '/genres'
            ? ''
            : currentpath;
      let paramsKey = currentParams.slice(
         currentParams.indexOf('=') + 1,
         currentParams.indexOf('&'),
      );

      paramsKey = isNaN(paramsKey) ? '' : paramsKey;

      return (
         <div>
            <Layout
               className="layout"
               style={{
                  position: 'fixed',
                  top: 0,
                  left: 0,
                  right: 0,
                  zIndex: 3,
               }}
            >
               <Header>
                  <Menu
                     selectedKeys={[currentpath + paramsKey]}
                     onClick={this.handleClick}
                     mode="horizontal"
                     theme="dark"
                     overflowedIndicator={<BarsOutlined />}
                  >
                     <Menu.Item
                        icon={
                           <img
                              className="header-logo"
                              src={SFCINEMA}
                              onClick={() => this.props.history.push('/')}
                           />
                        }
                        style={{
                           paddingRight: '30px',
                        }}
                        onClick={() => this.props.history.push('/')}
                     />
                     <Menu.Item
                        icon={<HomeOutlined />}
                        key="/"
                        onClick={() => this.props.history.push('/')}
                     >
                        
                     </Menu.Item>

                     <SubMenu
                        icon={<MenuUnfoldOutlined />}
                        title="SF "
                        mode="horizontal"
                        key="/genres"
                        style={{
                           paddingRight: '200px',
                        }}
                     >
                        {geners.map((gener, i) => (
                           <Menu.Item
                              key={i + 40 + ''}
                              icon={gener[1]}
                              style={{}}
                              onClick={({ key }) =>
                                 this.NavigateToGenres('/genres', key, gener[0])
                              }
                           >
                              {gener[0]}
                           </Menu.Item>
                        ))}
                     </SubMenu>

                     <Menu.Item
                        icon={<VideoCameraFilled />}
                        title=" "
                        key="/releaseOrder"
                        style={{}}
                        onClick={({ key }) =>
                           this.NavigateToHighlyRataedAndReleaseOrder(
                              '/releaseOrder',
                              key,
                              100,
                              20220000,
                              20200000,
                           )
                        }
                     >
                         
                     </Menu.Item>

                     <Menu.Item
                        icon={<StarFilled />}
                        title="  "
                        key="/highlyRated"
                        onClick={({ key }) =>
                           this.NavigateToHighlyRataedAndReleaseOrder(
                              '/highlyRated',
                              key,
                              100,
                              10,
                              8.5,
                           )
                        }
                     >
                          
                     </Menu.Item>

                     <Menu.Item
                        icon={<GiftFilled />}
                        key="/operatorMovies"
                        onClick={({ key }) =>
                           this.NavigateToOperatorAndMasterpiece(
                              '/operatorMovies',
                              key,
                              41,
                           )
                        }
                     >
                         
                     </Menu.Item>

                     <Menu.Item
                        icon={<CrownFilled />}
                        key="/masterpiece"
                        onClick={({ key }) =>
                           this.NavigateToOperatorAndMasterpiece(
                              '/masterpiece',
                              key,
                              100,
                           )
                        }
                     >
                        SF 
                     </Menu.Item>
                     <SubMenu
                        icon={<SketchCircleFilled />}
                        title="SF "
                        mode="horizontal"
                        key="/series"
                     >
                        <SubMenu
                           key="/seriesMenuItem-1"
                           title=" / "
                        >
                           {marvelSeriesList.map((name, i) => (
                              <Menu.Item
                                 key={i + ''}
                                 onClick={({ key }) =>
                                    this.NavigateToSeries('/series', key, name)
                                 }
                              >
                                 {name}
                              </Menu.Item>
                           ))}
                        </SubMenu>

                        {/* key=16  */}
                        <SubMenu
                           key="/seriesMenuItem-2"
                           title="/ "
                        >
                           {zombieAndMonsterSeriesList.map((name, i) => (
                              <Menu.Item
                                 key={i + 10 + ''}
                                 onClick={({ key }) =>
                                    this.NavigateToSeries('/series', key, name)
                                 }
                              >
                                 {name}
                              </Menu.Item>
                           ))}
                        </SubMenu>
                        {/* key=19  */}
                        <SubMenu
                           key="/seriesMenuItem-3"
                           title="/  "
                        >
                           {alienAndSpaceSeriesList.map((name, i) => (
                              <Menu.Item
                                 key={i + 16 + ''}
                                 onClick={({ key }) =>
                                    this.NavigateToSeries('/series', key, name)
                                 }
                              >
                                 {name}
                              </Menu.Item>
                           ))}
                        </SubMenu>
                        {/* key=23  */}
                        <SubMenu
                           key="/seriesMenuItem-4"
                           title="/ "
                        >
                           {actionAndAdventureSeriesList.map((name, i) => (
                              <Menu.Item
                                 key={i + 32 + ''}
                                 onClick={({ key }) =>
                                    this.NavigateToSeries('/series', key, name)
                                 }
                              >
                                 {name}
                              </Menu.Item>
                           ))}
                        </SubMenu>
                     </SubMenu>

                     {!isLogin ? (
                        <Menu.Item
                           icon={<FormOutlined />}
                           key="/signUp"
                           onClick={() => this.props.history.push('/signUp')}
                           style={{ float: 'right' }}
                        >
                           
                        </Menu.Item>
                     ) : null}

                     {!isLogin ? (
                        <Menu.Item
                           icon={<LoginOutlined />}
                           key="/login"
                           onClick={() => this.props.history.push('/login')}
                           style={{ float: 'right' }}
                        >
                           
                        </Menu.Item>
                     ) : null}

                     {isLogin ? (
                        <Menu.Item
                           icon={<UserOutlined />}
                           key="/profile"
                           onClick={this.showDrawer}
                           style={{
                              float: 'right',
                           }}
                        >
                            
                        </Menu.Item>
                     ) : null}
                  </Menu>
               </Header>
            </Layout>

            <Drawer
               title={<img src={SFCINEMA} className="max-small-logo" />}
               width={350}
               closable={true}
               onClose={this.onClose}
               visible={this.state.visible}
               footer={
                  <div
                     style={{
                        margin: '0 0 15pt 40pt',
                     }}
                  >
                     <div>
                        <Button
                           type="primary"
                           htmlType="submit"
                           style={{
                              width: '180pt',
                              margin: '10pt 0 0 0',
                              borderRadius: '3px',
                           }}
                           onClick={this.handleLogoutChange}
                        >
                           
                        </Button>
                     </div>
                  </div>
               }
            >
               <MyInfo
                  isLogin={this.props.isLogin}
                  profile={this.state.profile}
                  handleLoginChange={handleLoginChange}
               />
            </Drawer>
         </div>
      );
   }
}
// eslint-disable-next-line
export default withRouter(MenuBar);
// eslint-disable-next-line
